import useLocalStorage from "@hooks/useLocalStorage";
import userRoutes from "@src/navigation/vertical";
import { useEffect, useState } from "react";
import roles from "@src/constants/roles";

export default function useRoutes() {
    const [userData] = useLocalStorage("userData", null);
    const [routes, setRoutes] = useState([]);
    const [canAddRes, setCanAddRes] = useState(false);
    const authAddRes = [roles.INPUTTER, roles.CHECKER, roles.ADMIN];

    useEffect(() => {
        if (!userData || !userData.roleId) return;
        const result = filterRoutes(userRoutes, userData.roleId);
        setRoutes(result);
        if (authAddRes.find((role) => role === userData.roleId))
            setCanAddRes(true);
        else setCanAddRes(false);
    }, [userData]);

    return [routes, canAddRes];
}

function filterRoutes(routes = userRoutes, role = null) {
    let result = routes;
    if (!role) return result;
    routes.forEach((item, index) => {
        if (item.children)
            result[index].children = filterRoutes(item.children, role);
        else if (
            item.auth &&
            !item.auth.find((authRole) => authRole === role)
        ) {
            result = result.filter((i) => i.id !== item.id);
        }
    });

    return result;
}
