// ** React Imports
import { Fragment } from 'react'

import { injectIntl } from 'react-intl'
// ** Custom Components
import NavbarUser from './NavbarUser'
import NavbarBookmarks from './NavbarBookmarks'

const ThemeNavbar = props => {
  // ** Props
  const {intl, skin, setSkin, activeItem, menuVisibility, setMenuVisibility } = props

  return (
    <Fragment>
      <div className='bookmark-wrapper d-flex align-items-center'>
        <NavbarBookmarks setMenuVisibility={setMenuVisibility} />
        <span style={{fontSize: "2rem", fontWeight: 500, paddingLeft: "1rem"}}>{activeItem && intl.formatMessage({id: activeItem})}</span>
      </div>
      {/* {!menuVisibility && <button onClick={() => setMenuVisibility(prevState => !prevState)}>toggle menu</button>} */}
      <NavbarUser skin={skin} setSkin={setSkin} />
    </Fragment>
  )
}

export default injectIntl(ThemeNavbar)
