import { lazy } from 'react'
import roles from '@src/constants/roles'
const {ADMIN, INPUTTER, CHECKER, DATA_ADMIN, SALE} = roles
const PagesRoutes = [
  {
    path: '/dashboard/PagesSidebar/Team',
    component: lazy(() => import('@src/views/dashboard/PagesSidebar/Team')),
  },
  {
    path: '/dashboard/PagesSidebar/CleanedLand',
    component: lazy(() => import('@src/views/pages/cleaned-res/ResCleanedList')),
    auth: [ADMIN,CHECKER],
  },
  {
    path: '/dashboard/PagesSidebar/WaitingApproval',
    component: lazy(() => import('@src/views/tables/RES_waiting_approval')),
    auth: [ADMIN, DATA_ADMIN],
  },
  {
    path: '/dashboard/PagesSidebar/Rawland',
    component: lazy(() => import('@src/views/dashboard/PagesSidebar/Rawland')),
    auth: [ADMIN, INPUTTER]
  },
  {
    path: '/dashboard/PagesSidebar/Alertland',
    component: lazy(() => import('@src/views/dashboard/PagesSidebar/Alertland')),
    auth: [ADMIN, INPUTTER, CHECKER],
  },
  {
    path: '/view/:id',
    component: lazy(() => import('@src/views/pages/edit-res')),
  },
  {
    path: '/detail/:id',
    component: lazy(() => import('@src/views/pages/detail-res/ResDetail')),
    auth: [DATA_ADMIN, SALE]
  },
  {
    path: '/dashboard/PagesSidebar/Selectedland',
    component: lazy(() => import('@src/views/dashboard/PagesSidebar/Selectedland')),
    auth: [ADMIN, INPUTTER, CHECKER]
  },
  {
    path: '/dashboard/PagesSidebar/Job',
    component: lazy(() => import('@src/views/dashboard/PagesSidebar/Job')),
  },
  {
    path: '/dashboard/PagesSidebar/Statistical',
    component: lazy(() => import('@src/views/dashboard/PagesSidebar/Statistical')),
  },
  {
    path: '/dashboard/PagesSidebar/Custom',
    component: lazy(() => import('@src/views/dashboard/PagesSidebar/Custom')),
  },
  {
    path: '/dashboard/PagesSidebar/Customer',
    component: lazy(() => import('@src/views/dashboard/PagesSidebar/Customer')),
    auth: [ADMIN, SALE]
  },
  {
    path: '/dashboard/PagesSidebar/Report',
    component: lazy(() => import('@src/views/dashboard/PagesSidebar/Report')),
  },
  {
    path: '/dashboard/PagesSidebar/VerifiedRes',
    component: lazy(() => import('@src/views/dashboard/PagesSidebar/VerifiedRes')),
    auth: [ADMIN, SALE],
  },
  {
    path: '/login',
    component: lazy(() => import('../../views/pages/authentication/Login')),
    layout: 'BlankLayout',
    meta: {
      authRoute: true
    }
  },
  {
    path: '/forgot-password',
    component: lazy(() => import('../../views/pages/authentication/ForgotPassword')),
    layout: 'BlankLayout',
    meta: {
      authRoute: true
    }
  }
]

export default PagesRoutes
